import Layout from "@/views/layout/Layout";
import empty from "@/views/layout/empty";
import commonList from '@/views/common/list/index';
import addLimit from '@/views/logisticsManage/addLimit';
// 
import driverDetail from '@/views/logisticsManage/driverDetail';
export default {
  path: '/logistics-manage',
  name: 'logisticsManage',
  component: Layout,
  meta: {
    title: '',
    icon: 'icon icon-shezhi'
  },
  children: [
    // 
    {
        path: 'driver/list',
        name: 'driver',
        component: commonList,
        meta: {title: ''}
    },
    {
        path: 'driver/detail',
        name: 'driverDetail',
        component: driverDetail,
        hidden: true
    },
    // 
    {
        path:'limit-quota',
        name:'limitQuota',
        component: empty,
        meta: {title: ''},
        children:[
          // 
          {
            path: 'enterprise',
            name: 'enterpriseDayQuota',
            component: commonList,
            meta: {title: ''}
          },
          {
            path: 'enterprise/add',
            name: 'enterpriseDayQuotaAdd',
            component: addLimit
          },
          // 
          {
            path: 'vehicle',
            name: 'vehicleDayQuota',
            component: commonList,
            meta: {title: ''}
          },
          {
            path: 'vehicle/add',
            name: 'vehicleDayQuotaAdd',
            component: addLimit
          },
        ]
    },
    // 
     {
        path: 'quota',
        name: 'enterpriseQuota',
        component: commonList,
        meta: {title: ''}
      },
      {
        path: 'available-credit',
        name: 'availableCredit',
        component: commonList,
        hidden: true
      },
      {
        path: 'day-credit',
        name: 'dayCredit',
        component: commonList,
        hidden: true
      },
      // 
      // {
      //   path: 'capacity/list',
      //   name: 'vehicleCapacity',
      //   component: commonList,
      //   meta: {title: ''}
      // },
      // {
      //   path: 'capacity/detail',
      //   name: 'vehicleCapacityDetail',
      //   component: commonList,
      //   hidden: true
      // }
  ]
}