import request from "@/utils/request";
import system from "./modules/system";
import basicInfo from "./modules/baseic-info";
import oilStationManage from "./modules/oil-station-manage";
export default {
  // -
  logistics: (page, pageSize, name, status, out_status) => {
    const data = {
      page,
      pageSize,
      search: {
        name,
        out_status,
        status
      }
    };
    return request({
      url: "/webAPI/LogisticsCompany/list",
      method: "post",
      data
    });
  },
  //-
  vehicleData: (page, pageSize, logistics_name, number) => {
    const data = {
      page,
      pageSize,
      search: {
        logistics_name,
        number
      }
    };
    return request({
      url: "/webAPI/Vehicle/list",
      method: "post",
      data
    });
  },
  //-
  driver: (page, pageSize, driver_name, status, logistics_name) => {
    const data = {
      page,
      pageSize,
      search: {
        driver_name,
        status,
        logistics_name
      }
    };
    return request({
      url: "/webAPI/driver/list",
      method: "post",
      data
    });
  },
  // -
  enterpriseDayQuota: (
    page,
    pageSize,
    create_time_start,
    create_time_end,
    code
  ) => {
    const data = {
      page,
      pageSize,
      search: {
        create_time_start,
        create_time_end,
        code
      }
    };
    return request({
      url: "/webAPI/LogisticsQuotaLimit/list",
      method: "post",
      data
    });
  },
  // -
  vehicleDayQuota: (
    page,
    pageSize,
    create_time_start,
    create_time_end,
    code
  ) => {
    const data = {
      page,
      pageSize,
      search: {
        create_time_start,
        create_time_end,
        code
      }
    };
    return request({
      url: "/webAPI/VehicleQuotaLimit/list",
      method: "post",
      data
    });
  },
  // -
  enterpriseQuota: (page, pageSize, logistics_name, status) => {
    const data = {
      page,
      pageSize,
      search: {
        status,
        logistics_name
      }
    };
    return request({
      url: "/webAPI/LogisticsQuota/list",
      method: "post",
      data
    });
  },
  // --
  availableCredit: (
    page,
    pageSize,
    category,
    create_time_start,
    create_time_end,
    logistics_id
  ) => {
    const data = {
      page,
      pageSize,
      search: {
        category,
        create_time_start,
        create_time_end,
        logistics_id
      }
    };
    return request({
      url: "/webAPI/LogisticsQuotaLog/getByLogisticsId",
      method: "post",
      data
    });
  },
  // --
  dayCredit: (page, pageSize, logistics_id) => {
    const data = {
      page: page,
      pageSize,
      search: {
        logistics_id
      }
    };
    return request({
      url: "/webAPI/logisticsDailyQuotaLog/getByLogisticsId",
      method: "post",
      data
    });
  },
  // -
  vehicleCapacity: (page, pageSize, logistics_name, number) => {
    const data = {
      page: page,
      pageSize,
      search: {
        logistics_name,
        number
      }
    };
    console.log(data);
    return request({
      url: "/webAPI/LogisticsDailyQuotaLog/getByLogisticsId",
      method: "post",
      data
    });
  },
  ...system,
  ...basicInfo,
  ...oilStationManage
};
