<template>
  <div>
    <card>
      <span slot="title"></span>
      <item-list :com-data="currentInfo"/>
    </card>
    <card>
        <span slot="title"></span>
        <p style="line-height: 32px;">{{text1}}  {{text2}}<el-input style="width:100px;margin:0 5px;display:inline-block;" type="text" v-model="val"/>%</p>
    </card>
  </div>
</template>

<script>
import {
  logisticsQuotaLimit,
  vehicleQuotaLimit
} from "@/api/logisticsManage/limitQuota";
export default {
    data(){
        const textMap1 = {
            enterpriseDayQuotaAdd:'',
            vehicleDayQuotaAdd:''
        };
        const textMap2 = {
            enterpriseDayQuotaAdd:'',
            vehicleDayQuotaAdd:''
        };
        return {
            val:0,
            text1:textMap1[this.$route.name],
            text2:textMap2[this.$route.name],
            currentInfo: {
                data:{},
                list:[],
                styleObj:'width:100%;'
            }
        }
    },
    watch:{
        'val':function(newVal){
            this.$store.dispatch(`${this.$route.name}:update`,newVal)
        }
    },
    mounted(){
        this.currentInfo.list = [
            {
                label:`${this.text1}`,
                prop:'rate'
            },
            {
                label:'',
                prop:'create_time'
            }
        ]
        this.$store.dispatch(`${this.$route.name}:update`,this.val)
        const apiMap = {
            enterpriseDayQuotaAdd:logisticsQuotaLimit,
            vehicleDayQuotaAdd:vehicleQuotaLimit
        };
        apiMap[this.$route.name]()
        .then(res => {
            if(res.data && res.data.rate){
                this.val = (Number(res.data.rate)*100).toFixed(2);
                res.data.rate = (Number(res.data.rate)*100).toFixed(2) + '%';
            }
            this.currentInfo.data = res.data || {}
        })
    }
}
</script>
